body {
	background: #222;
	color: #eee;
}

.App {
	text-align: center;
}

.App-title {
	font-size: 26px;
}

/*Profile*/

.profile {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.profile-info {
	margin-left: 10px;
	text-align: left;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.profile-img {
	width: 150px;
	height: 150px;
	border: 3px solid #eee;
	border-radius: 75px;
	-o-object-fit: cover;
	   object-fit: cover;
}

.profile-name {
	font-size: 26px;
}

.profile-followers, .profile-genres {
	font-size: 18px;
}

/*Gallery*/

.track {
	display: inline-block;
	width: 220px;
	height: 220px;
	margin: 10px;
	text-align: left;
	cursor: pointer;
	position: relative;
}

.track-img {
	width: 220px;
	height: 220px;
	border: 3px solid #eee;
	border-radius: 3px;
	-o-object-fit: cover;
	   object-fit: cover;
	position: absolute;
}

.track-text {
	background: #111;
	color: white;
	opacity: 0.75;
	width: 214px;
	margin-left: 3px;
	text-align: center;
	position: absolute;
	bottom: 0;
	padding: 10px;
}

.track-play {
	position: absolute;
	width: 220px;
	height: 220px;
}

.track-play-inner {
	position: absolute;
	text-align: center;
	font-size: 25px;
	background-color: #111;
	border-radius: 30px;
	width: 60px;
	height: 60px;
	color: #eee;
	padding-top: 13px;
	margin-left: 80px; 
	margin-top: 80px;
	opacity: 0;
}

.track-play-inner:hover {
	opacity: 0.83;
	-webkit-animation-name: fade-in;
	        animation-name: fade-in;
	-webkit-animation-duration: 0.4s;
	        animation-duration: 0.4s; 
}

@-webkit-keyframes fade-in {
	from{
		opacity: 0;
	}
	to {
		opacity: 0.83;
	}
}

@keyframes fade-in {
	from{
		opacity: 0;
	}
	to {
		opacity: 0.83;
	}
}


